 <template>

   <div class="hidden" >


     <vs-navbar square  centerCollapsed text-white class="navibar-top" style="z-index:2000;position: relative;background: #000000;">
       <template #left>
            <img src="@/assets/img/logo.png" class="poolgo-logo-top">
       </template>

       <template #right>
        
         <el-dropdown  v-if="!!account"  @command="switchBalance">
         <vs-button icon textWhite style="background-color: rgba(0,0,0,0);border:1px solid #ffffff;line-height:19px;" gradient>{{ balance}}<i class='bx bx-down-arrow'></i></vs-button>

           <el-dropdown-menu slot="dropdown" >
             <el-dropdown-item command="CFX" >CFX</el-dropdown-item>
             <el-dropdown-item command="PCFX" >PCFX</el-dropdown-item>
           </el-dropdown-menu>
         </el-dropdown>
         <vs-button v-if="!!account" textWhite icon style="background-color: rgba(0,0,0,0);border:1px solid #ffffff;">{{ shortAddr(account) }}<i class='bx bxs-user'></i> </vs-button>
         <vs-button v-else icon style="background-color: rgba(0,0,0,0);border:1px solid #ffffff;"textWhite  @click="login()">{{$t('unlock')}}<i class='bx bx-lock-open'></i></vs-button>
       

       </template>

     </vs-navbar>


     <vs-navbar square  centerCollapsed text-white class="navibar-bottom" style="z-index:2000;position: relative;background: #000000;">
       <template #left>
            <img src="@/assets/img/logo.png" class="poolgo-logo-bottom">
       </template>


       <template #right>
        <!--<vs-navbar-item :active="$route.path == '/vaults'" id="license" to="/vaults" style="width:70px;text-align:center;">
           Vaults
         </vs-navbar-item>-->

         <vs-navbar-item :active="$route.path == '/farm'" id="guide" to="/farm" style="width:70px;text-align:center;">
           {{$t('farm')}}
         </vs-navbar-item>
         <!--<vs-navbar-item :active="$route.path == '/me'" id="license" to="/me" style="width:70px;text-align:center;">
           {{$t('me')}}
         </vs-navbar-item>-->

        <vs-navbar-item :active="$route.path == '/swap'" id="swap" to="/swap" style="width:70px;text-align:center;">
           Swap
         </vs-navbar-item>

        <!--<vs-navbar-item :active="$route.path == '/myvault'" id="myvault" to="/myvault" style="width:100px;text-align:center;">
           My Vaults
         </vs-navbar-item>-->

         
        <div class="hide-600 left-panding">
         <el-dropdown  v-if="!!account"  @command="switchBalance">
         <vs-button icon textWhite class="hide-600" style="background-color: rgba(0,0,0,0);border:1px solid #ffffff;line-height:19px;" gradient>{{ balance}}<i class='bx bx-down-arrow'></i></vs-button>

           <el-dropdown-menu slot="dropdown">
             <el-dropdown-item command="CFX" >CFX</el-dropdown-item>
             <el-dropdown-item command="PCFX" >PCFX</el-dropdown-item>
           </el-dropdown-menu>
         </el-dropdown>
        <vs-button v-if="!!account" textWhite icon class="hide-600" style="background-color: rgba(0,0,0,0);border:1px solid #ffffff;">{{ shortAddr(account) }}<i class='bx bxs-user'></i> </vs-button>
         <vs-button v-else icon class="hide-600" style="background-color: rgba(0,0,0,0);border:1px solid #ffffff;"textWhite  @click="login()">{{$t('unlock')}}<i class='bx bx-lock-open'></i></vs-button>
        
        </div>

        

      
       </template>

     </vs-navbar>


     <vs-dialog width="300px" not-center prevent-close v-model="switchNetworkDialogShow">
       <template #header>
         <h4 class="not-margin">
           {{$t('noticeSentece5Part1')}}<b>{{$t('noticeSentece5Part2')}}</b>
         </h4>
       </template>

       <div class="con-content">
         <p>
           {{$t('noticeSentece6')}}

         </p>
       </div>

       <template #footer>
         <div class="con-footer">
           <vs-button @click="switchNetworkDialogShow=false" transparent>
             {{$t('noticeConfirm')}}
           </vs-button>
         </div>
       </template>
     </vs-dialog>
   </div>



</template>

<script>
  import { mapActions } from 'vuex'
  import collapse from '@/utils/collapse.js'
  import utils from '@/utils/utils.js'
  import {getLocalStorage,setLocalStorage} from '@/utils/local-storage.js'
  import config from "@/config";
  // import blockie from "../componentsMine/Blockie";
  export default {
    name: 'home-view',
    components: {
      collapse
    },
    data() {
      return {
        isActive: false,
        active: 'home',
        activeSidebar: false,
        token:'PCFX',
        switchNetworkDialogShow:false,
      }
    },
    computed: {

      balance(){
        if (this.token == 'PCFX') {
          return this.$store.state.web3.pcfxBalance.toFixed(1) + "PCFX"
        }else{
          return this.$store.state.web3.balance.toFixed(1) + "CFX"
        }
      },
      account() {
        return this.$store.state.web3.account
      }
    },
    watch: {
      '$store.state.web3.chainId': function () {
        let that = this;
        if ( that.$store.state.web3.chainId != parseInt(config.networkId)){
          that.switchNetworkDialogShow = true;
        
        }
      }
    },
    created(){
      let data = getLocalStorage('language');
      if (data!=null && data.language!=null){
        this.$i18n.locale = data.language
      }
      let tokenData = getLocalStorage('token');
      if (tokenData!=null && tokenData.token!=null){
        this.token = tokenData.token
      }
      // this.init()
    },
    methods: {
      ...mapActions(['cfxLogin','logout']),
      showTwitterPage(){
        window.location.href = ''
      },
      login() {
        let that = this;

        if (that.$store.state.web3.account == null) {
          that.cfxLogin();
        }
        if (that.$store.state.web3.isPortalInstalled == false) {
          that.$vs.notification({
            // width:'100%',
            duration: 10000,
            icon: "<i class='bx bx-error' ></i>",
            progress: 'auto',
            color: 'danger',
            position: 'top-center',
            title: that.$t('error'),
            text: that.$t('noticeWallet'),
          })
          return;
        }
      },
      exit(){
        this.logout();
      },
      shortAddr(account) {
        return utils.shortAddr(account)
      },
      switchBalance(command){
        this.token = command;
        setLocalStorage({token:command})
        // this.$vuetify.lang.current  = command
      },
      switchLanguage(command){
        this.$i18n.locale = command;
        setLocalStorage({language:command})
        // this.$vuetify.lang.current  = command
      },
      showPoolPage(){
        this.$router.push({
          path:'/pools'
        })
      },
    }
  }
</script>

<style>
  .hidden {
    border-bottom: 2px solid #15171c;
  }
  .collapse-menu {
    position: absolute;
    top: 80px;
    width: 100%;
    text-align: left;
  }
  .collapse-menu div {
    padding: 5px 10px;
    color: #76807F;
  }
  .heading {
    text-align: center;
    margin-bottom: 0px;
    background: #14151A;
  }
  .header-bar {
  	display: flex;
  	justify-content: space-between;
  	font-size: 1rem;
  	height: 70px;
  	padding-top: 20px;
  	padding-left: 20px;
  	padding-right: 20px;
  }
  .header-bar .el-button {
    -webkit-box-align: center;
    align-items: center;
    background-color: rgb(33, 33, 33);
    border: 0px;
    border-radius: 12px;
    color: rgb(144, 144, 255);
    cursor: pointer;
    display: flex;
    font-size: 14px;
    font-weight: 700;
    height: 36px;
    -webkit-box-pack: center;
    justify-content: center;
    outline: none;
    padding-left: 16px;
    padding-right: 16px;
    width: 120px;

  }
  .header-bar .logo {
  	height: 45px;
  }
  .header-bar .menu {
  	height: 45px;
    color: #eee;
    text-align: center;
    padding-top: 10px;
  }
  .header-bar .menu span {
    padding: 0 20px;
    font-weight: bold;
  }
  .header-bar .menu ul {
    padding: 0px;
  }
  .header-bar .menu ul li {
  	display: inline-block;
  	font-size: 1rem;
  	height: 45px;
  	padding: 15px;
  	font-weight: bold;
  }
  .vs-navbar .active {
    color: rgb(233, 30, 99);
  }
  .vs-navbar__line {
    background: rgb(233, 30, 99) !important;
  }

  .poolgo-logo-top{
    width:200px;
  }

  .poolgo-logo-bottom {
    width: 130px;
  }

  @media screen  and (min-width:601px) {
    .poolgo-logo-top{
      display: none;
    }
    .navibar-top {
      display: none;
    }
    .navibar-bottom {
      height:72px; 
      padding-top: 13px;
    }
    .left-panding {
      display: flex;
      justify-content:  flex-end;
      margin-left: 25px;
      width: 245px;
    }
  }
  @media screen  and (max-width:600px) {
    .poolgo-logo-top{
      width: 120px;
    }
    .poolgo-logo-bottom {
      display: none;
    }
    .hide-600 {
      display: none;
    }
    .navibar-top {
      height:70px; 
      padding-top: 10px;
    }
    .navibar-bottom {
      height: 50px;
      padding-top: 5px;
    }

  }
</style>
