import {toBN, BN, isBN} from 'web3-utils'
import {MAX_UINT256} from './Constants'
import {BigNumber} from 'bignumber.js'
import config from '@/config'
import {Conflux, format} from 'js-conflux-sdk';
import utils from "@/utils/utils";

BigNumber.set({ROUNDING_MODE: 1})

export class AdminControl {
    constructor() {
        this.conflux = window.confluxJS;
        this.confluxReader = new Conflux({
            url: config.web3Provider,
            networkId:  config.networkId
        });
        if (this.conflux != null){
            this.contract = this.conflux.InternalContract('AdminControl');
        }
        this.defaultGasPrice = 20000000000;
    }


    async destroy(sender,contract_addr) {
        try {

            const receipt = await this.contract.destroy(contract_addr).sendTransaction({
                from: sender
            }).executed();
            // let weiAmount = BigNumber(amount).times(BigNumber(10).pow(BigNumber(this.decimals)));
            // let weiAmountBN = toBN(weiAmount);
            //   debugger;
            return utils.contractResult(true,receipt);
            //   console.log('receipt', JSON.stringify(receipt, null, 2));
        } catch (e) {
            console.log(e);
            return utils.contractResult(false,e);
        }
    }

    async setAdmin(sender,contract_addr,new_addr) {
        try {

            const receipt = await this.contract.setAdmin(contract_addr,new_addr).sendTransaction({
                from: sender,
            }).executed();
            // let weiAmount = BigNumber(amount).times(BigNumber(10).pow(BigNumber(this.decimals)));
            // let weiAmountBN = toBN(weiAmount);
            //   debugger;
            return utils.contractResult(true,receipt);
            //   console.log('receipt', JSON.stringify(receipt, null, 2));
        } catch (e) {
            console.log(e);
            return utils.contractResult(false,e);
        }
    }
}

export default AdminControl;
