
export default class Connector {

	// coinType - BNB ETH CFX
	constructor(coinType) {
		this.coinType = coinType.toUpperCase()
	}

	async connect() {
		let provider
		if (this.coinType == 'BNB') {
			if (window['BinanceChain']) {
				window['detectProvider'] = window['BinanceChain']
				provider = window['detectProvider']
				try {
					await window['detectProvider'].enable()
				} catch (e) {
					console.error(e)
					return
				}
			} 
		} else if (this.coinType == 'ETH') {
			if (window['ethereum']) {
				window['detectProvider'] = window['ethereum']
				provider = window['detectProvider']
				try {
					await window['detectProvider'].enable()
				} catch (e) {
					console.error(e)
					return
				}
			} else if (window['web3']) {
				provider = window['web3'].currentProvider
			}
		} else if (this.coinType == 'CFX') {
			if (window['conflux']) {
				provider = window['conflux']
			}
			try {
				await window['conflux'].enable()
			} catch (e) {
				console.error(e)
				return
			}
		}
		return provider
	}

	isLoggedIn() {
		if (this.coinType == 'BNB') {

			return !!window['BinanceChain']

		} else if (this.coinType == 'ETH') {

			return !!window['ethereum']

		} else if (this.coinType == 'CFX') {

			return !!window['conflux']
		} 

		return false
	}
}