
import {toBN, BN, isBN} from 'web3-utils'
import utils from '@/utils/utils'
import {BigNumber} from 'bignumber.js'

BigNumber.set({DECIMAL_PLACES: 18, ROUNDING_MODE: 1})
import config from '@/config'
import {Conflux, format} from 'js-conflux-sdk';

const  { abi, bytecode } = require('./abis/LaunchField.json');


export class StakePool {
    constructor(solidityAddress) {

        
        this.conflux = window.confluxJS;
        this.confluxReader = new Conflux({
            url: config.web3Provider,
            networkId:  config.networkId
        });

        this.address = solidityAddress;
        if (this.conflux != null)
            this.contract = this.conflux.Contract({abi, bytecode, address: solidityAddress})
        this.contractReader = this.confluxReader.Contract({abi, bytecode, address: solidityAddress});
        this.defaultGasPrice = 20000000000;
        this.decimals = 18;
    }

    setProvider(provider) {
        this.conflux.setProvider(provider)
    }

    async totalSupply() {
        let supply = await this.contractReader.totalSupply();
        return BigNumber(supply).div(
            BigNumber(10).pow(BigNumber(this.decimals)));
    }

    async gasPrice() {
        return this.defaultGasPrice;
    }
    async reBegin(sender) {

        try {
            // let weiAmount = BigNumber(amount).times(this.stakePrecision);
            var tx = this.contract.reBegin(100000000000000000,0,0,10000);
            var estimate = await tx.estimateGasAndCollateral({from: sender});
            const receipt = await tx.sendTransaction({
                from: sender,
                value: 0,
                gas: format.big(estimate.gasUsed).times(1.1).toFixed(0), // you could multiply `gas` like this
                storageLimit: estimate.storageCollateralized,
                gasPrice: 1,
            }).executed();
            return utils.contractResult(true,receipt);
            //   console.log('receipt', JSON.stringify(receipt, null, 2));
        } catch (e) {
            return utils.contractResult(false,e);
        }

    }
    async stake(sender, amount) {
        try {
            let weiAmount = BigNumber(amount).times(BigNumber(10).pow(BigNumber(this.decimals)));
            let weiAmountBN = toBN(weiAmount);
            var tx = this.contract.stake(weiAmountBN);
            var estimate = await tx.estimateGasAndCollateral({from: sender});
            const receipt = await tx.sendTransaction({
                from: sender,
                value: 0,
                gas: format.big(estimate.gasUsed).times(1.1).toFixed(0), // you could multiply `gas` like this
                storageLimit: estimate.storageCollateralized,
                gasPrice: 1,
            }).executed();
            return utils.contractResult(true,receipt);
            //   console.log('receipt', JSON.stringify(receipt, null, 2));
        } catch (e) {
            return utils.contractResult(false,e);
        }

    }

    async earned(sender) {
        let earned = await this.contractReader.earned(sender);
        return BigNumber(earned).div(
            BigNumber(10).pow(BigNumber(this.decimals)));
    }

    async balanceOf(sender) {
        let balance = await this.contractReader.balanceOf(sender);
        return BigNumber(balance).div(
            BigNumber(10).pow(BigNumber(this.decimals)));
    }
    async rewardRate() {
        let balance = await this.contractReader.rewardRate();
        return BigNumber(balance).div(
            BigNumber(10).pow(BigNumber(this.decimals)));
    }

    async periodFinish() {
        return await this.contractReader.periodFinish();
    }

    async duration() {
        return await this.contractReader.halvingPeriod();
    }

    async lastUpdateTime() {
        return await this.contractReader.lastUpdateTime();
    }

    async startTime() {
        return await this.contractReader.starttime();
    }

    async finishTime() {
        return await this.contractReader.eraPeriod();
    }

    async initreward() {
        let initReward = await this.contractReader.totalreward();
        return BigNumber(initReward).div(
            BigNumber(10).pow(BigNumber(this.decimals)));
    }


    async getReward(sender) {

        try {
            var tx = this.contract.getReward();
            var estimate = await tx.estimateGasAndCollateral({from: sender});
            const receipt = await tx.sendTransaction({
                from: sender,
                value: 0,
                gas: format.big(estimate.gasUsed).times(1.1).toFixed(0), // you could multiply `gas` like this
                storageLimit: estimate.storageCollateralized,
                gasPrice: 1,
            }).executed();
            return utils.contractResult(true,receipt);
            //   console.log('receipt', JSON.stringify(receipt, null, 2));
        } catch (e) {
            return utils.contractResult(false,e);
        }
    }

    async exit(sender) {
        try {
            var tx = this.contract.exit();
            var estimate = await tx.estimateGasAndCollateral({from: sender});
            const receipt = await tx.sendTransaction({
                from: sender,
                value: 0,
                gas: format.big(estimate.gasUsed).times(1.1).toFixed(0), // you could multiply `gas` like this
                storageLimit: estimate.storageCollateralized,
                gasPrice: 1,
            }).executed();
            return utils.contractResult(true,receipt);
            //   console.log('receipt', JSON.stringify(receipt, null, 2));
        } catch (e) {
            return utils.contractResult(false,e);
        }
    }

    async withdraw(sender, amount) {

        try {
            let weiAmount = BigNumber(amount).times(this.stakePrecision);
            var tx = this.contract.withdraw(toBN(weiAmount));
            var estimate = await tx.estimateGasAndCollateral({from: sender});
            const receipt = await tx.sendTransaction({
                from: sender,
                value: 0,
                gas: format.big(estimate.gasUsed).times(1.1).toFixed(0), // you could multiply `gas` like this
                storageLimit: estimate.storageCollateralized,
                gasPrice: 1,
            }).executed();
            return utils.contractResult(true,receipt);
            //   console.log('receipt', JSON.stringify(receipt, null, 2));
        } catch (e) {
            return utils.contractResult(false,e);
        }

    }
}

export default StakePool;
