import {toBN, BN, isBN} from 'web3-utils'
import {MAX_UINT256} from './Constants'
import {BigNumber} from 'bignumber.js'
import config from '@/config'
import {Conflux, format} from 'js-conflux-sdk';
import utils from "@/utils/utils";

BigNumber.set({ROUNDING_MODE: 1})
const {abi, bytecode} = require('./abis/UniswapV2Pair.json');

export class UniswapV2Pair {
    constructor(solidityAddress) {
        this.confluxReader = new Conflux({
            url: "https://mainnet-rpc.conflux-chain.org.cn/v2",
            networkId:  1029
        });
        // this.confluxReader = new Conflux({
        //     url: config.web3Provider,
        //     networkId:  config.networkId
        // });
        this.address = solidityAddress;
        this.contractReader = this.confluxReader.Contract({abi, bytecode, address: solidityAddress});
        this.defaultGasPrice = 20000000000;
    }

    async token0(){
        let result = await this.contractReader.token0();
        return result;
    }

    async token1(){
        let result = await this.contractReader.token1();
        return result;
    }
    async getPrice(){
        let result = await this.contractReader.getReserves();
        let reserve0 = new BigNumber(result[0]);
        let reserve1 = new BigNumber(result[1]);
        return reserve0.div(reserve1);
    }
    async getReversalPrice(){
        let result = await this.contractReader.getReserves();
        let reserve0 = new BigNumber(result[0]);
        let reserve1 = new BigNumber(result[1]);
        return reserve1.div(reserve0);
    }
}

export default UniswapV2Pair;
