import {toBN, BN, isBN} from 'web3-utils'
import {MAX_UINT256} from './Constants'
import {BigNumber} from 'bignumber.js'
import config from '@/config'
import {Conflux, format} from 'js-conflux-sdk';
import utils from "@/utils/utils";

BigNumber.set({ROUNDING_MODE: 1})
const {abi, bytecode} = require('./abis/PCFX.json');

export class PCFX {
    constructor(solidityAddress) {
        this.conflux = window.confluxJS;
        this.confluxReader = new Conflux({
            url: config.web3Provider,
            networkId:  config.networkId
        });
        this.address = solidityAddress;
        if (this.conflux != null)
            this.contract = this.conflux.Contract({abi, bytecode, address: solidityAddress})
        this.contractReader = this.confluxReader.Contract({abi, bytecode, address: solidityAddress});
        this.defaultGasPrice = 20000000000;
    }

    async allowance(owner,spender){
        let result = await this.contractReader.allowance(owner,spender);
        return new BigNumber(result).div(
            BigNumber(10).pow(BigNumber(18)));
    }
    async balanceOf(user) {
        let result = await this.contractReader.balanceOf(user);
        return new BigNumber(result).div(
            BigNumber(10).pow(BigNumber(18)));
    }

    async approveMax(sender, spender) {
        try {
            var tx = this.contract.approve(spender, MAX_UINT256);
            var estimate = await tx.estimateGasAndCollateral({from: sender});
            const receipt = await tx.sendTransaction({
                from: sender,
                value: 0,
                gas: format.big(estimate.gasUsed).times(1.1).toFixed(0), // you could multiply `gas` like this
                storageLimit: estimate.storageCollateralized,
                gasPrice: 1,
            }).executed();
            //   debugger;
            return utils.contractResult(true,receipt);
            //   console.log('receipt', JSON.stringify(receipt, null, 2));
        } catch (e) {
            console.log(e);
            return utils.contractResult(false,e);
        }
    }

    async approve(sender, spender,amount) {
        try {
            let weiAmount = BigNumber(amount).times(BigNumber(10).pow(BigNumber(18)));
            let weiAmountBN = toBN(weiAmount);
            var tx = this.contract.approve(spender, weiAmountBN);
            var estimate = await tx.estimateGasAndCollateral({from: sender});
            const receipt = await tx.sendTransaction({
                from: sender,
                value: 0,
                gas: format.big(estimate.gasUsed).times(1.1).toFixed(0), // you could multiply `gas` like this
                storageLimit: estimate.storageCollateralized,
                gasPrice: 1,
            }).executed();
            //   debugger;
            return utils.contractResult(true,receipt);
            //   console.log('receipt', JSON.stringify(receipt, null, 2));
        } catch (e) {
            console.log(e);
            return utils.contractResult(false,e);
        }
    }
    async deposit(sender, amount) {
        try {
            let weiAmount = BigNumber(amount).times(BigNumber(10).pow(BigNumber(18)));
            let weiAmountBN = toBN(weiAmount);
            var tx = this.contract.deposit();
            // var estimate = await tx.estimateGasAndCollateral({from: sender});
            // debugger;
            const receipt = await tx.sendTransaction({
                from: sender,
                value: weiAmountBN
            }).executed();
            //   debugger;
            return utils.contractResult(true,receipt);
            //   console.log('receipt', JSON.stringify(receipt, null, 2));
        } catch (e) {
            console.log(e);
            return utils.contractResult(false,e);
        }
    }
    async withdraw(sender, amount) {
        try {
            let weiAmount = BigNumber(amount).times(BigNumber(10).pow(BigNumber(18)));
            let weiAmountBN = toBN(weiAmount);
            var tx = this.contract.withdraw(weiAmountBN);
            var estimate = await tx.estimateGasAndCollateral({from: sender});
            const receipt = await tx.sendTransaction({
                from: sender,
                value: 0,
                gas: format.big(estimate.gasUsed).times(1.1).toFixed(0), // you could multiply `gas` like this
                storageLimit: estimate.storageCollateralized,
                gasPrice: 1,
            }).executed();
            //   debugger;
            return utils.contractResult(true,receipt);
            //   console.log('receipt', JSON.stringify(receipt, null, 2));
        } catch (e) {
            console.log(e);
            return utils.contractResult(false,e);
        }
    }
}

export default PCFX;
