import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'swap',
    component:  () => import(/* webpackChunkName: "pools" */ '../views/Swap.vue')
  },
  /*
  {
    path: '/debug',
    name: 'debug',
    component: () => import('../views/Debug.vue')
  },*/
  {
    path: '/swap',
    name: 'Swap',
    component:() => import( /* webpackChunkName: "swap" */ '../views/Swap.vue')
  },
  {
    path: '/vaults',
    name: 'Vaults',
    component:() => import( /* webpackChunkName: "vaults" */ '../views/Vaults.vue')
  },
  {
    path: '/farm',
    name: 'Farm',
    component:() => import(/*  webpackChunkName: "farm" */  '../views/Farm.vue')
  },
  {
    path: '/myvault',
    name: 'MyVault',
    component:() => import(/*  webpackChunkName: "farm" */  '../views/MyVault.vue')
  },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  }
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

export default router
