
import {toBN, BN, isBN} from 'web3-utils'
import {MAX_UINT256} from './Constants'
import {BigNumber} from 'bignumber.js'
import config from '@/config'
import {Conflux, format} from 'js-conflux-sdk';
import utils from "@/utils/utils";
BigNumber.set({ROUNDING_MODE: 1})
const {abi, bytecode} = require('./abis/PrizePool.json');

export class PrizePool {
    constructor(solidityAddress) {
        this.conflux = window.confluxJS;
        this.confluxReader = new Conflux({
            url: config.web3Provider,
            networkId:  config.networkId
        });

        this.address = solidityAddress;
        if (this.conflux != null)
            this.contract = this.conflux.Contract({abi, bytecode, address: solidityAddress})
        this.contractReader = this.confluxReader.Contract({abi, bytecode, address: solidityAddress});
        this.defaultGasPrice = 20000000000;
        this.decimals = 18;
    }

    async calculateEarlyExitFee(from,controlledToken,amount) {
        let feeAmount = await this.contractReader.calculateEarlyExitFee(from,controlledToken,amount);
        return feeAmount;
    }

    async balanceOfCreditLastTime(from,controlledToken) {
        let result = await this.contractReader.balanceOfCreditLastTime(from,controlledToken);
        return new BigNumber(result).div(
            BigNumber(10).pow(BigNumber(this.decimals)));
    }
    async balanceOfCredit(from,controlledToken) {
        let result = await this.contractReader.balanceOfCredit(from,controlledToken);
        return result;
    }
    async accountedBalance() {
        let result = await this.contractReader.accountedBalance();
        let bigResult = new BigNumber(result);
        return bigResult.div(
            BigNumber(10).pow(BigNumber(this.decimals)));
    }
    async awardBalance() {
        let result = await this.contractReader.awardBalance();
        return new BigNumber(result).div(
            BigNumber(10).pow(BigNumber(this.decimals)));
    }

    async tokens() {
        let tokens = await this.contractReader.tokens();
        return tokens;
    }

    async timelockTotalSupply() {
        let result = await this.contractReader.timelockTotalSupply();
        return new BigNumber(result).div(
            BigNumber(10).pow(BigNumber(this.decimals)));
    }
    async reserveTotalSupply() {
        let result = await this.contractReader.reserveTotalSupply();
        return new BigNumber(result).div(
            BigNumber(10).pow(BigNumber(this.decimals)));
    }
    async timelockBalanceOf(user) {
        let result = await this.contractReader.timelockBalanceOf(user);
        return new BigNumber(result).div(
            BigNumber(10).pow(BigNumber(this.decimals)));
    }
    async timelockBalanceAvailableAt(user) {
        let result = await this.contractReader.timelockBalanceAvailableAt(user);
        return new BigNumber(result);
    }


    async cToken() {
        let cToken = await this.contractReader.cToken;
        return cToken;
    }

    async captureAwardBalance(sender) {
        try {
            var tx =  this.contract.captureAwardBalance();
            var estimate = await tx.estimateGasAndCollateral({from: sender});
            const receipt = await tx.sendTransaction({
                from: sender,
                value: 0,
                gas: format.big(estimate.gasUsed).times(1.1).toFixed(0), // you could multiply `gas` like this
                storageLimit: estimate.storageCollateralized,
                gasPrice: 1,
            }).executed();

            for (const log of receipt.logs) {
                const eventArg = this.contract.abi.decodeLog(log);
                console.log(JSON.stringify(eventArg, null, 2));
            }
            //   debugger;
            return utils.contractResult(true,receipt);
            //   console.log('receipt', JSON.stringify(receipt, null, 2));
        } catch (e) {
            console.log(e);
            return utils.contractResult(false,e);
        }
    }
    async withdrawInstantlyFrom(sender,amount, controlledToken,maximumExitFee) {
        try {
            let weiAmount = BigNumber(amount).times(BigNumber(10).pow(BigNumber(this.decimals)));
            let weiAmountBN = toBN(weiAmount);
            let maximumExitFeeAmount = BigNumber(maximumExitFee).times(BigNumber(10).pow(BigNumber(this.decimals)));
            let maximumExitFeeBN = toBN(maximumExitFeeAmount);
            var tx =  this.contract.withdrawInstantlyFrom(sender,weiAmountBN, controlledToken,maximumExitFeeBN);
            var estimate = await tx.estimateGasAndCollateral({from: sender});
            const receipt = await tx.sendTransaction({
                from: sender,
                value: 0,
                gas: format.big(estimate.gasUsed).times(1.1).toFixed(0), // you could multiply `gas` like this
                storageLimit: estimate.storageCollateralized,
                gasPrice: 1,
            }).executed();
            //   debugger;
            return utils.contractResult(true,receipt);
            //   console.log('receipt', JSON.stringify(receipt, null, 2));
        } catch (e) {
            console.log(e);
            return utils.contractResult(false,e);
        }
    }

    async depositTo(sender,to,amount, controlledToken,referrer) {
        try {
            let weiAmount = BigNumber(amount).times(BigNumber(10).pow(BigNumber(this.decimals)));
            let weiAmountBN = toBN(weiAmount);
            var tx = this.contract.depositTo(to, weiAmountBN, controlledToken, referrer);
            var estimate = await tx.estimateGasAndCollateral({from: sender});
            const receipt = await tx.sendTransaction({
                from: sender,
                value: 0,
                gas: format.big(estimate.gasUsed).times(1.1).toFixed(0), // you could multiply `gas` like this
                storageLimit: estimate.storageCollateralized,
                gasPrice: 1,
            }).executed();
            //   debugger;
            return utils.contractResult(true, receipt);
            //   console.log('receipt', JSON.stringify(receipt, null, 2));
        } catch (e) {
            console.log(e);
            return utils.contractResult(false, e);
        }
    }
    async getFiveAwardInfo(){
        let result = [];
        let awardInfo = await this.contractReader.getLastAwardInfo(5);
        for (let i=0;i<awardInfo[0].length;i++){
            let itemAmount = new BigNumber(awardInfo[1][i]).div(BigNumber(10).pow(BigNumber(this.decimals)));
            let item = {amount:itemAmount,addr:format.address(awardInfo[0][i],config.networkId)}
            result.push(item)
        }
        // console.log(result)
        return result;
    }

    async getUserAwardInfo(user){

        let result = [];
        let awardInfo = await this.contractReader.getUserAwardInfo(user);
        // console.log(awardInfo)
        for (let i=0;i<awardInfo.length;i++){
            let itemAmount = new BigNumber(awardInfo[i]).div(BigNumber(10).pow(BigNumber(this.decimals)));
            let item = {amount:itemAmount}
            result.push(item)
        }
        // console.log(result)
        return result;
    }
    async withdrawWithTimelockFrom(sender,from,amount, controlledToken) {
        try {
            let weiAmount = BigNumber(amount).times(BigNumber(10).pow(BigNumber(this.decimals)));
            let weiAmountBN = toBN(weiAmount);
            var tx = this.contract.withdrawWithTimelockFrom(from, weiAmountBN, controlledToken);
            var estimate = await tx.estimateGasAndCollateral({from: sender});
            const receipt = await tx.sendTransaction({
                from: sender,
                value: 0,
                gas: format.big(estimate.gasUsed).times(1.1).toFixed(0), // you could multiply `gas` like this
                storageLimit: estimate.storageCollateralized,
                gasPrice: 1,
            }).executed();

            receipt.decodeLogs = [];
            for (const log of receipt.logs) {
                const eventArg = this.contract.abi.decodeLog(log);
                if (eventArg!=undefined && eventArg!=null)
                    receipt.decodeLogs.push(eventArg);
                // console.log(JSON.stringify(eventArg, null, 2));
            }
            return utils.contractResult(true, receipt);
            //   console.log('receipt', JSON.stringify(receipt, null, 2));
        } catch (e) {
            console.log(e);
            return utils.contractResult(false, e);
        }
    }
    async sweepTimelockBalances(sender,users){
        try {
            var tx = this.contract.sweepTimelockBalances(users);
            var estimate = await tx.estimateGasAndCollateral({from: sender});
            const receipt = await tx.sendTransaction({
                from: sender,
                value: 0,
                gas: format.big(estimate.gasUsed).times(1.1).toFixed(0), // you could multiply `gas` like this
                storageLimit: estimate.storageCollateralized,
                gasPrice: 1,
            }).executed();
            return utils.contractResult(true, receipt);
            //   console.log('receipt', JSON.stringify(receipt, null, 2));
        } catch (e) {
            console.log(e);
            return utils.contractResult(false, e);
        }
    }
    async timelockDepositTo(sender,to,amount, controlledToken) {
        try {
            let weiAmount = BigNumber(amount).times(BigNumber(10).pow(BigNumber(this.decimals)));
            let weiAmountBN = toBN(weiAmount);
            var tx = this.contract.timelockDepositTo(to, weiAmountBN, controlledToken);
            var estimate = await tx.estimateGasAndCollateral({from: sender});
            const receipt = await tx.sendTransaction({
                from: sender,
                value: 0,
                gas: format.big(estimate.gasUsed).times(1.1).toFixed(0), // you could multiply `gas` like this
                storageLimit: estimate.storageCollateralized,
                gasPrice: 1,
            }).executed();
            return utils.contractResult(true, receipt);
            //   console.log('receipt', JSON.stringify(receipt, null, 2));
        } catch (e) {
            console.log(e);
            return utils.contractResult(false, e);
        }
    }
}

export default PrizePool;
