function etherscanAddr(addr) {
    return `https://bscscan.com/address/${addr}`;
}

function etherscanTx(txid) {
    return `https://bscscan.com/tx/${txid}`;
}

function formatDate(str) {
    return str; //`${str.substr(0, 4)}/${str.substr(4, 2)}/${str.substr(6, 2)}`
}

function shortAddr(addr) {
    return addr.substr(0, 10) + '...' + addr.substr(-4)
}

function contractResult(succ, msg) {
    return {succ: succ, ...msg};
}
function formatSeconds(value) {
    var theTime = parseInt(value);// 秒
    var theTime1 = 0;// 分
    var theTime2 = 0;// 小时
    if (theTime > 60) {
        theTime1 = parseInt(theTime / 60);
        theTime = parseInt(theTime % 60);
        if (theTime1 > 60) {
            theTime2 = parseInt(theTime1 / 60);
            theTime1 = parseInt(theTime1 % 60);
        }
    }

    var result = "" + parseInt(theTime);//秒
    // if (10 > theTime > 0) {
    //     result = "0" + parseInt(theTime)+"s";//秒
    // } else {
        result = "" + parseInt(theTime)+"s";//秒
    // }

    // if (10 > theTime1 > 0) {
    //     result = "0" + parseInt(theTime1) + "m" + result;//分，不足两位数，首位补充0，
    // } else {
        result = "" + parseInt(theTime1) + "m" + result;//分
    // }
    if (theTime2 > 0) {
        result = "" + parseInt(theTime2) + "h" + result;//时
    }
    return result;
}
export default {
    etherscanAddr,
    etherscanTx,
    formatDate,
    shortAddr,
    contractResult,
    formatSeconds,
}